import React from 'react'
import NavButton from './nav-button'
import { Twitter, HomeFill } from './icons'

import styles from './nav.module.css'

function Navigation() {
  return (
    <nav>
      <NavButton>
        <Twitter />
      </NavButton>
      <NavButton>
        <HomeFill>
          <span>Home</span>
        </HomeFill>
      </NavButton>
    </nav>
  )
}

export default Navigation
